.sidenav {
    flex: 3;
    height: fit-content;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #c7eaea;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .sidebarTitle {
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: solid 1px #a7a4a4;
    border-bottom: solid 1px #a7a4a4;
    text-align: center;
    font-family: "Varela Round", sans-serif;
    font-size: 12px;
    line-height: 19px;
    color: #222222;
    font-weight: 600;
  }
  .sidebarItem > img {
    margin-top: 15px;
    width: 250px;
    height: 250px;
  }

  .sidebarItem > p {
    padding: 30px;
  }
  
  .sideNavList {
    list-style-type: none;
    margin-bottom: 30px;
  }
  .sidenavListItem {
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
  }